import $ from "jquery";
import 'jquery-ui-bundle';
import 'jquery-ui-bundle/jquery-ui.css';
import igv from "./igv-crispy.js";

$('#select_all_btn').click( function() {
  selectAll();
});

var toggleAll = false;

function selectAll() {
 $('input[name="checked_ids[]"]').each(function(){
    if(toggleAll == true) {
      $(this).prop('checked', false);
    }
    else {
      $(this).prop('checked', true);
    }
 });
 toggleAll = !toggleAll;
}

$('#export_btn').click( function () {
  function reload(){
    document.getElementById("export_form").reset();
  };
  window.setTimeout( reload, 10 );

});

//highlight instances of offsite hits predicted
$('.offsite_cnt').each(function() {
  if (parseInt($(this).text()) > 0) {
      $(this).css("background-color","red");
  }
});

let div = $("#browser")[0];
let fastaURL = "/S288C_reference_sequence_R64-1-1_20110203.fasta"
let genesBed = "/yeast_genes.bed"
let geneTrackName = "S. cerevisiae S288C Genes"

if ('#{@strain.name}' == 'GLBRCY22-3') {
  fastaURL = "/GLBRCY22-3.fasta"
  genesBed = "/y22-3_genes.bed"
  geneTrackName = "S. cerevisiae GLBRCY22-3 Genes"
}
else if ('#{@strain.name}' == 'ZM4') {
  fastaURL = "/ZYMOMONAS.fasta"
  genesBed = "/ZYMOMONAS.bed"
  geneTrackName = "Z. Mobilis ZM4 Genes"
}

const options = {
        palette: ["#00A0B0", "#6A4A3C", "#CC333F", "#EB6841"],
        locus: "#{@chrom_start}:#{@locus_start}-#{@locus_end}",

        reference: {
            id: "S288C",
            fastaURL: fastaURL
        },

        trackDefaults: {
            bam: {
                coverageThreshold: 0.2,
                coverageQualityWeight: true
            }
        },

        tracks: [
            {
                name: "Generated sgRNAs Forward Strand",
                url: "/generated_sgrna_coords_pos.bed",
                displayMode: "EXPANDED"
            },
            {
                name: geneTrackName,
                color: "red",
                url: genesBed,
                displayMode: "EXPANDED"
            },
            {
                name: "Generated sgRNAs Reverse Strand",
                url: "/generated_sgrna_coords_neg.bed",
                color: "purple",
                displayMode: "EXPANDED"
            },
            {
                name: "S. cerevisiae Introns",
                color: "DarkOrange",
                url: "/yeast_introns.bed",
                displayMode: "EXPANDED"
            }
        ]
    };
const browser = igv.createBrowser(div, options);

function zoomViewer(position) {
  const startCoord = parseInt(position) - 10;
  const len = parseInt("#{@sgrna_length}");
  const endCoord = parseInt(position) + len + 10;
  const locusString = "#{@chrom_start}:" + startCoord + "-" + endCoord;
  igv.browser.search(locusString);
  $("html, body").animate({ scrollTop: 0 }, "medium");
}
